import React, { Component } from 'react';
import axios from 'axios';

class CreateContact extends Component {
    constructor(){
        super()
        this.state = {
            nome:'',
            telefone:'',
            celular:'',
            email:'',
            endereco:''
        }
        this.changeNome = this.changeNome.bind(this)
        this.changeTelefone = this.changeTelefone.bind(this)
        this.changeCelular = this.changeCelular.bind(this)
        this.changeEmail = this.changeEmail.bind(this)
        this.changeEndereco = this.changeEndereco.bind(this)
        this.onSubmit = this.onSubmit.bind(this)
    }
    changeNome(event){
        this.setState({
            nome:event.target.value
        })
    }
    changeTelefone(event){
        this.setState({
            telefone:event.target.value
        })
    }
    changeCelular(event){
        this.setState({
            celular:event.target.value
        })
    }
    changeEmail(event){
        this.setState({
            email:event.target.value
        })
    }
    changeEndereco(event){
        this.setState({
            endereco:event.target.value
        })
    }

    onSubmit(event){
        event.preventDefault()

        const registered = {
            nome:this.state.nome,
            telefone:this.state.telefone, 
            celular:this.state.celular,
            email:this.state.email,
            endereco:this.state.endereco
        }

        axios.post('http://localhost:3000/createContact', registered)
        .then(response => console.log(response.data))

        this.setState({
            nome:'',
            telefone:'',
            celular:'',
            email:'',
            endereco:''
        })
    }

    render() {
        return (
            <div>
              <div className="container">
                  <div className="form-div">
                     <form onSubmit={this.onSubmit}>
                         <input type="text" placeholder="Nome" onChange={this.changeNome}value={this.state.nome} className="form-control form-group" />

                         <input type="tel" placeholder="Telefone fixo" onChange={this.changeTelefone}value={this.state.telefone} className="form-control form-group" />

                         <input type="tel" placeholder="Celular" onChange={this.changeCelular}value={this.state.celular} className="form-control form-group" />

                         <input type="email" placeholder="Email" onChange={this.changeEmail}value={this.state.email} className="form-control form-group" />

                         <input type="text" placeholder="EndereÃ§o" onChange={this.changeEndereco}value={this.state.Endereco} className="form-control form-group" /> 

                         <input type="submit" className="btn btn-danger btn-block" value="submit" />    
                     </form>
                  </div>
              </div>
            </div>
        );
    }
}


export default CreateContact;